import { Sequelize, DataTypes, Model } from "sequelize";
import sequelize from '../../middlewares/dbInstance'
import Student from './Student'
import Course from './Course'



const Enroll = sequelize.define('Enroll', {
  course_id: {
    type: DataTypes.NUMBER,
    references: {
      model: Course,
      key: 'id'
    }
  },
  student_id: {
    type: DataTypes.NUMBER,
    references: {
      model: Student,
      key: 'id'
    }
  }
}, {
  tableName: 'students',
  timestamps: false
})

// Enroll.belongsTo(Course)
// Enroll.belongsTo(Student)

export default Enroll